@include mq(small){
    .container{
        box-sizing: content-box;
        border:5px solid black;
        max-width:80vw;
        height:100vh;
       margin-left:auto;
       margin-right:auto;
        display:flex;
        flex-direction:column;
        overflow:hidden;
       
    }
    .containerNight{
        transition:background-color 3s;
        
        background-color: lighten(black,10%);
        transition:3s;
        color:darkorange;
    }
    .containerDay{
        transition:background-color 3s;
        background-color: darken(white,5%);
        transition:3s;
        color:black;
    }
  
/*Main*/
main{
    flex-grow:1;
    }
    /*MEdia*/
.media{
    width:80vw;
    height:7%;
    background-color:red;
    position:fixed;
    bottom:0%;
    display:flex;
}
.facebook{
    flex-grow:1;
    background-color: lighten(blue,10%);
    height:100%;
    color:white;
    display:flex;
    justify-content: center;
    align-items: center;
}
.youtube{
    color:white;
  flex-grow:1;
    height:100%;
    display:flex;
    justify-content: center;
    align-items: center;
}
.google{
    background-color: lighten(green,5%);
    color:white;
    flex-grow:1;
    height:100%;
    display:flex;
    justify-content: center;
    align-items: center;
   
}
.linkss{
    color:white;
    font-weight:bold;
    letter-spacing: 4px;
}
}